html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

.sol {
  min-width: 100vw;
  width: 100vw;
  max-width: 100vw;
  min-height: 100vh;
  height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch; }
  .sol p,
  .sol a,
  .sol span {
    font-family: 'Zilla Slab', serif;
    font-size: 1em; }
  .sol h1, .sol h2, .sol h3,
  .sol h4, .sol h5, .sol h6 {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    text-align: center;
    text-shadow: 1px 1px 1px #000; }
